<template>
  <div id="app">
    <canvas></canvas>
    <Header @change="onChangeComponent" />
    <Loading v-show="isLoading" />
    <transition
      v-on:before-enter="beforeEnter"
      v-on:enter="enter"
      v-on:leave="leave"
    >
      <component :is="componentId" ></component>
    </transition>
  </div>
</template>

<script>
import HelloWorld from './components/HelloWorld.vue'
import Header from './components/Header.vue'
import About from './components/About.vue'
import Loading from './components/Loading.vue'
import Skill from './components/Skills.vue'
import Service from './components/Service.vue'
import Vue from 'vue';
import transitions from './utils/transition'

Vue.mixin(transitions)

export default {
  name: 'App',
  components: {
    HelloWorld,
    Header,
    About,
    Loading,
    Skill,
    Service
  },
  data() {
    return {
      isLoading: false,
      componentId: 'HelloWorld'
    }
  },
  mounted() {
    setTimeout(() => {
      document.querySelectorAll('button').forEach((item) => {
        item.addEventListener('mouseup', (e) => {
          if (e && e.target) {
            e.target.blur();
          }
        });
      });
    });
    const canvas = document.querySelector('canvas');
    const ww = window.innerWidth;
    const wh = window.innerHeight;
    canvas.width = ww;
    canvas.height = wh;

    let c = canvas.getContext('2d');
    const myColor = ['#F2BDD6', '#56738C', '#B6DBF2', "#04BFBF","#F2D680"];
    const mouse = {
      x: undefined,
      y: undefined
    }

    function Circle(x, y, dx, dy, radius, minRadius, maxRadius) {
      this.x = x;
      this.y = y;
      this.dx = dx;
      this.dy = dy;
      this.radius = radius;
      this.minRadius = minRadius;
      this.maxRadius = maxRadius;
      var rand = myColor[Math.floor(Math.random() * myColor.length)];

      this.draw = function() {
        c.beginPath();
        c.arc(this.x, this.y, this.radius, 0, Math.PI *2, false)
        c.fillStyle = rand;
        c.fill();
      }

      this.update = function() {
        if (this.x + this.radius > ww || this.x - this.radius < 0) {
          this.dx = -this.dx;
        }
        if (this.y + this.radius > wh || this.y - this.radius < 0) {
          this.dy = -this.dy;
        }
        this.x += this.dx;
        this.y += this.dy;
        this.draw();


        //interactive
        if (mouse.x - this.x < 50 && mouse.x - this.x > -50 && mouse.y - this.y < 50 && mouse.y - this.y > -50 ) {
          if (this.radius < this.maxRadius) {
            this.radius++;
          }
        } else if (this.radius > this.minRadius) {
          this.radius--;
        }
      }
    }

    window.addEventListener('mousemove', function(event){
      mouse.x = event.x;
      mouse.y = event.y;
    })

    window.addEventListener('resize', function(){
      canvas.width = window.innerWidth;
      canvas.height = window.innerHeight;
      init();
    })

    var cirlceArray = []

    function init() {
      cirlceArray = [];
      for(let i=1; i<500; i++) {
        const radius = Math.random() * 3 + 1;
        const minRadius = Math.random() * 3 + 1;
        const maxRadius = Math.random() * 30 + 10;
        const x = Math.random() * (ww - radius*2) + radius;
        const y = Math.random() * (wh - radius*2) + radius;
        const dx = Math.random() - 0.5;
        const dy = Math.random() - 0.5;
        cirlceArray.push(new Circle(x,y, dx, dy, radius, minRadius, maxRadius))
      }
    }

    function animate() {
      requestAnimationFrame(animate);
      c.clearRect(0, 0, ww, wh);
      cirlceArray.forEach((e) => {
      e.update();
      })
    }

    init();
    animate();
  },
  methods: {
    onChangeComponent(component) {
      if (this.componentId === component) { return; }
      this.isLoading = true;
      setTimeout(() => {
        this.isLoading = false;
        this.componentId = component;
      }, 1000)
    }
  }
}
</script>

<style>
.fade-enter-active, .fade-leave-active {
  transition: opacity .5s;
}
.fade-enter, .fade-leave-to /* .fade-leave-active below version 2.1.8 */ {
  opacity: 0;
}
#app {
  font-family: 'Montserrat', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #2c3e50;
}
.seperator {
  
  width: 50%;
  height: 2px;
  margin-bottom: 30px;
  background: #007bff;
}

html {
  overflow: hidden;
}

canvas {
  position: absolute;
  z-index: -1;
}
</style>
