<template>
  <section class="container">
    <div class="demo-content staggering-grid-demo">
    <div class="grid">
      <div v-for="i in 135" :key="i" class="small square el" style="transform: scale(1);"></div>
    </div>
  </div>
  </section>
</template>
<script>
import anime from 'animejs';
export default {
  mounted() {
    anime({
      targets: '.staggering-grid-demo .el',
      easing: 'linear',
      loop: true,
      scale: [
        {value: .1, easing: 'easeOutSine', duration: 200},
        {value: 1, easing: 'easeInOutQuad', duration: 400}
      ],
      delay: anime.stagger(200, {grid: [14, 5], from: 'center'})
    });
  }
}
</script>
<style scoped>
  .container {
    height: calc(100vh - 200px);
    margin-top: 50px;
    padding: 0;
    border-radius: 25px;
  }
  .grid {
    display: flex;
    flex-wrap: wrap;
  }
  .small.square {
    width: 66px;
    height: 66px;
    background: #EAF2FD;
    margin: 5px;
  }

  .shadow {
    opacity: 0;
  }
</style>